import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Engagements from '@/components/Engagements'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/#/mysite/engagements',
      name: 'Engagements',
      component: Engagements
    }
  ]
})



